.restaurantListDiv {
    display: flex;
    flex-direction: column;
  }
  
.restaurantDiv {
    display: flex;
    flex-direction: column;
    background-color: white;
    margin: 20px 10px 20px;
}
.restaurantDiv:hover {
    -moz-box-shadow:    3px 3px 5px 6px #ccc;
    -webkit-box-shadow: 3px 3px 5px 6px #ccc;
    box-shadow: 3px 3px 5px 6px #ccc;
}
.nameRatingDiv {
    display: flex;
    flex-direction: row;
}

.nameRatingDiv div {
    height: 80px;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.nameDiv {
    padding-left: 20px;
}
.nameDiv h3 {
    color: #00A2FF;
    text-align: left;
}
.ratingDiv p {
    color: #F8C448;
    font-weight: 800;
}

.imageAddressDiv {
    display: flex;
    flex-direction: column;
}
.addressDiv {
    padding-left: 20px;
}
.contactDiv {
    padding-left: 20px;
    margin-bottom: 20px;
}
.contactDiv a {
    color: #00A2FF;
}
.addressDiv h4, .contactDiv h4 {
    text-align: left;
    margin-bottom: 5px;
    color: #F8C448;
    font-weight: 800;
}
.addressDiv p, .contactDiv p {
    text-align: left;
    margin: 0;
    margin-bottom: 3px;
}
.contactIdDiv {
    display: flex;
    flex-direction: row;
}
.imageDiv {
    width: 100%;
}
.imageDiv img {
    width: 100%;
   
}

@media (min-width: 600px) {
    .restaurantListDiv {
        flex-direction: row;
        flex-wrap: wrap;
    }
    .restaurantDiv {
        width: 47%;
    }
}